import { Component, OnInit } from '@angular/core';
import { StorageService } from '../../services/storage.service';
@Component({
  selector: 'app-demo3',
  templateUrl: './demo3.component.html',
  styleUrls: ['./demo3.component.css', './animate.min.css']
})
export class Demo3Component implements OnInit {
  msg = '';
  list = [[], []];
  constructor(private storage: StorageService) { }
  ngOnInit() {
    if (this.storage.getItem('data')) {
      this.list = this.storage.getItem('data');
    }
  }
  add(key) {
    if (key.keyCode === 13 && this.msg !== '') {
      const cont = this.msg;
      this.list[0].push(cont);
      this.storage.setItem('data', this.list);
      this.msg = '';
    }
  }
  top(i, target) {
    $(target).parent().toggleClass('animated zoomOut');
    const _this = this;
    setTimeout(function () {
      _this.list[0].unshift(_this.list[0].splice(i, 1));
      _this.storage.setItem('data', _this.list);
    }, 250);
  }
  rm(i, target, from, to) {
    $(target).parent().toggleClass('animated zoomOut');
    const _this = this;
    setTimeout(function () {
      _this.list[to].push(_this.list[from].splice(i, 1));
      $(target).parent().toggleClass('animated bounceIn');
      _this.storage.setItem('data', _this.list);
    }, 300);
  }
  del(i, target, from) {
    $(target).parent().toggleClass('animated zoomOut');
    const _this = this;
    setTimeout(function () {
      _this.list[from].splice(i, 1);
      _this.storage.setItem('data', _this.list);
    }, 300);
  }
}
